body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form {
  margin: auto;
}

input, button {
  margin: 5px;
  width: 50%;
  border: 2px solid black;
  border-radius:5px;
  font-size: 18px;
}

.card {
	background: #F8F8F8;
	width:50%;
	border-radius:5px;
	border: solid #BDBDBD 1px;
	box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.5)  ;
	-webkit-box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.5)  ;
	-moz-box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.5)  ;

}

.card-title {
  font-size: 16px;
  font-weight: bold;
}

button {
  border-radius: 8px;
  width:180px;
}

img {
	padding-right:50px;
  height: 25px;
  width:25px;
}

button:hover,img:hover,input:hover {
  cursor:pointer;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.responses-container {
  display:flex;
  flex-direction: column;
  align-items:center;
  grid-gap:20px;
  padding:20px;
}

.App {
  border:0px;
  text-align: center;
  background-image: url('https://1.bp.blogspot.com/-6oCOl4MR8jg/Ua8Y0NjKedI/AAAAAAAAFMk/qzY41ie3BKM/s1600/light-blue-checkerboard-pattern.jpg')
}

h1{
  color:black;
  background:white;
}

